.btn {
    display: inline-block;
    vertical-align: top;
    padding: 16px;
    min-width: 200px;
    box-sizing: border-box;
    text-align: center;
    margin: 0;
    outline: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    cursor: pointer;
    background-color: black;
    color: #ffffff;
    font-size: 16px;
    line-height: 1.3;
    font-weight: 700;
    font-style: normal;
    border-radius: 3px;
    border: 1px solid black;
    text-transform: uppercase;
    transition: 0.3s ease;

    &:hover,
    &:focus {
        background-color: white;
        color: black;
    }
}

.btn--blue {
    background: $blue;
    color: white;
    border: 2px solid $blue;
    font-family: $alt-family, sans-serif;
    border-radius: 2px;
    &.btn--secondary {
        background: none;
        color: $blue;
        &:hover {
            background: $blue;
            color: white;
            transition: background color 0.2s ease;
        }
    }
}
.button-white {
    background: $lighter-gray;
    color: $dark_gray;
    border-radius: 2px;
    padding: 8px 0;
    width: 176px;
    text-align: center;
    text-transform: uppercase;
    font-family: $alt-family, sans-serif;
    font-weight: bold;
    cursor: pointer;
    &:hover {
        background: $orange;
    }
    img {
        vertical-align: middle;
    }
}
